







































import Vue from "vue";
import TaskMenu from "@/components/Todo/TaskMenu.vue";
import { format } from "date-fns";

export default Vue.extend({
  props: ["task"],
  filters: {
    niceDate(value: string): string {
      return format(new Date(value), "MMM d");
    }
  },
  components: {
    "task-menu": TaskMenu
  }
});
