




















import Vue from "vue";

export default Vue.extend({
  props: ["task"],
  data() {
    return {
      date: null
    };
  },
  methods: {
    saveTask() {
      const payload = {
        id: this.task.id,
        dueDate: this.date
      };
      this.$store.dispatch("updateTaskDueDate", payload);
      this.$emit("close");
    }
  },
  mounted() {
    if (this.task.dueDate) {
      this.date = this.task.dueDate;
    }
  }
});
